(function () {
    const $ = function (el) {
        if (Array.isArray(el)) {
            return document.querySelectorAll(el);
        }

        return document.querySelector(el);
    };

    const slider = $(".airlines__slider");

    

    function updateSlide() {

        const sliderContent = slider.querySelectorAll(".airlines__slider-content"),

            contentElem = slider.querySelector(".airlines__slider-content"),

            animationDuration =

                window.getComputedStyle(contentElem).animationDuration,

            sliderDuration = parseInt(animationDuration) * 1000;

    

    

        setInterval(() => {

            sliderContent[0].remove();

            slider.appendChild(contentElem);

        }, sliderDuration);

    }

    

    updateSlide();
    const statsRatio = document.querySelectorAll(".stats__item-ratio"),
        statsSection = document.querySelector(".stats"),
        scrollUntil = statsSection.offsetTop - statsSection.clientHeight;
    
    function watchScroll() {
    
        if (window.scrollY >= scrollUntil) {
            statsRatio.forEach((stat) => {
                let statVal = +stat.innerHTML,
                    speed = 5;
    
                function incrementRatio(i = 0) {
                    stat.innerHTML = i;
                    i++;
    
                    if (i > statVal * 0.5 && i < statVal * 0.75) {
                        speed = 20;
                    } else if (i >= statVal * 0.75 && i < statVal * 0.85) {
                        speed = 40;
                    } else if (i >= statVal * 0.85 && i < statVal * 0.95) {
                        speed = 60;
                    } else if (i >= statVal * 0.95) {
                        speed = 100;
                    }
    
                    if (i <= statVal) {
                        setTimeout(() => incrementRatio(i), speed);
                    }
                }
    
                incrementRatio();
    
                document.removeEventListener("scroll", watchScroll);
            });
        }
    }
    
    document.addEventListener("scroll", watchScroll);
    
    const scrollBtn = document.querySelector('.scrollTop');
    
    scrollBtn.addEventListener('click', () => {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    })
    
    document.addEventListener('scroll', e => {
        if (window.scrollY > 1000) {
            scrollBtn.classList.add('show');
        } else {
            scrollBtn.classList.remove('show');
        }
    })
    
})();

